@import url('https://fonts.googleapis.com/css?family=Noto+Sans+KR:100');
.about_me {
  display: flex;
  justify-content: flex-start;
  margin: 0;
  background: white;
  height: 100%;
  width: 100%;
}

.about_me_title {
  margin-left: 6vw;
  margin-right: 12vw;
  width: 25vw;
  box-sizing: border-box;
  text-align: center;
  align-self: center;
}
.hedding {
  position: relative;
  display: inline-block;
  width: 20vw;
  height: 20vw;
  margin: 30px 0;
}
.hedding::after {
  position: absolute;
  content: '';
  top: 100%;
  height: 2px;
  width: 50px;
  left: 0;
  right: 0;
  margin: 0 auto;
  background: rgb(123, 124, 212);
}

.about_me_content {
  font-family: 'Noto Sans KR', sans-serif;
  margin-bottom: 4rem;
  margin-top: 4rem;
  box-sizing: border-box;
  padding: 1rem;
  margin-right: 8vw;
  width: 100%;
}

@media (max-width: 576px) {
  .about_me {
    display: inherit;
    height: 100%;
    width: 100%;
    text-align: center;
  }
  .about_me_title {
    display: inline-block;
    text-align: center;
    margin: 0 auto;
  }
  .about_me_content {
    display: inline-block;
    margin-top: 0px;
    width: 100%;
  }
}
